import { MajorInfo } from '../../@types/majorData'

const searchTheme = (searchFor: string, majorArr: MajorInfo[]) => {
  const result = majorArr.filter((elem: MajorInfo) => {
    return elem.theme === searchFor
  })

  return result
}

export default searchTheme
