import { MajorInfo } from '../../@types/majorData'

const searchMajor = (searchFor: string, majorArr: MajorInfo[]) => {
  const result = majorArr.find((elem: MajorInfo) => {
    return elem.name === searchFor
  })

  return result
}

export default searchMajor
